.counterMain {
    width: 240px;
    height: 215px;
    border: 2px solid lightgrey;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.counterSum {
    width: 100%;
    height: 150px;
    border: solid 1px lightgrey;
    border-radius: 10px;
    background-color: lightgrey;

    color: darkslategrey;
    font-size: 90px;
    font-weight: 900;

    display: flex;
    align-items: center;
    justify-content: center;
}

.counterSumStop {
    color: #e10000;
    font-size: 120px;
    font-weight: 900;
}

.buttonWrapper {
    width: 100%;
    height: 50px;
    border: solid 1px lightgrey;
    border-radius: 10px;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.valuesIsNotCorrect {
    color: #e10000;
    font-size: 16px;
    font-weight: 700;
}